<template>
  <div class="apply-wrap">
    <van-nav-bar left-arrow @click-left="fnBack" v-show="!invisiableBack" class="apply-wrap-header" />
    <skeleton v-if="isSkeletonLoading" :nums="4" fheight="4rem" iheight="2rem"></skeleton>
    <div v-else class="container">
      <div class="top">
        <div class="title">
          <div class="big">欢迎加入{{interfaceTitle}}合伙人！</div>
          <div class="normal">完成以下资料并通过认证，即可成为{{interfaceTitle}}合伙人</div>
        </div>
        <div class="progress">
          <van-steps :active="progressIndex" style="width:auto">
            <van-step v-for="(item,index) in progressInfo" :key="index">
              <template slot="active-icon">
                <img class="active-icon" :src="`${$cdnImageUrl}/partner/active-h.png`" />
              </template>
              <template slot="inactive-icon">
                <img class="inactive-icon" :src="progressIndex<index?`${$cdnImageUrl}/partner/inactive-h.png`:`${$cdnImageUrl}/partner/done.png`" />
              </template>
              <p class="title" :style='{opacity:progressIndex<index?"0.7":"1"}'>{{item.title}}</p>
            </van-step>
          </van-steps>
        </div>
      </div>
      <div class="bottom">
        <div class="in-top">
          <!-- 实名阶段 -->
          <div v-if="stage <= 1">
            <stage-0
              :documentId="documentId"
              :name='name'
              :identify="identify"
              :mobile='displayMobile'
              :checked="checked"
              :readOnly="readOnly"
              :canEditMobile='canEditMobile'
              :isSyn='isSyn'
              @idChange='fnIdChange'
              @nameChange='fnNameChange'
              @mobileChange='fnMobileChange'
              @licenseChecked='fnLicenseChecked' />
          </div>
          <!-- 填写信息阶段 -->
          <div v-else-if="stage === 2">
            <stage-1
              :info="info"
              :userIdentify='userIdentify'
              :monthExpectSaleAmount='monthExpectSaleAmount'
              :showCommunity='showCommunity'
              :showPeople='showPeople'
              @nicknameChange='fnNicknameChange'
              @jobChange='fnJobChange'
              @communityChange='fnCommunityChange'
              @communityAdd='fnCommunityAdd'
              @communityClose='fnCommunityClose'
              @timeChange='fnDateChange'
              @locationChange='fnlocationChange'
              @monthExpectSaleAmountChange='fnMonthExpectSaleAmountChange'
              @peopleChange='fnPeopleChange'
              @peopleAdd='fnPeopleAdd'
              @peopleClose='fnPeopleClose'/>
          </div>
          <!-- 审核阶段 -->
          <div v-else-if="stage === 3">
            <stage-2
              :info='verifyInfo'
              :index='verifyIndex'
              :cdn="$cdnImageUrl"
              @reApply='fnReApply' />
          </div>
        </div>
        <div @click="fnNextStep" v-if="stage !== 3" class="button">{{stage === 2 ? '提交' : '下一步'}}</div>
      </div>
    </div>
    <van-popup v-model="showDateChange" round position="bottom" :style="{ height: '40%' }">
      <van-datetime-picker
        @confirm='fnConfirmDate'
        @cancel='showDateChange=false'
        v-model="date"
        type="year-month"
        title="选择年月"
        :min-date="minDate"
        :max-date="maxDate"
        :formatter="_formatter"
      />
    </van-popup>
    <van-popup v-model="showChooseLoaction" position="bottom" :style="{height:'9.6rem', 'border-radius': '12px 12px 0 0'}"  @close="fnOnHide" @click="fnOnHide" get-container="#app-warp">
       <lazy-change-geographic-location v-if='showChooseLoaction' :isShowBack="false" title="选择地址" @onHide="fnOnHide" @handleLocation="fnConfirmLocation" />
    </van-popup>
    <van-popup v-model="showJobs" :close-on-click-overlay='false' position="bottom" :style="{height:'100%'}" get-container="#app-warp">
       <jobs v-if='showJobs' @confirm="fnConfirmJobChange" @cancel='fnOnHide'></jobs>
    </van-popup>
    <van-dialog v-model="showFailDialog" show-cancel-button>
      <span>实名认证不通过，</span>
      <span>可联系<router-link to="/mine">在线客服</router-link>!</span>
    </van-dialog>
  </div>
</template>

<script>
import skeleton from '@/components/skeleton/index.vue'
import Stage0 from './component/stage0.vue'
import Stage1 from './component/stage1.vue'
import Stage2 from './component/stage2.vue'
import item from './component/formItem'
import utils from '@/utils/utils'
import { Step, Steps, Dialog } from 'vant';
// 接口
import { submitApplyInfo, applyResult, queryApplyInfo, bindWxAccount } from '@/http/service/partners'
import { myManageinfo } from '@/http/service/mine'
// import personalApiIndex from '@/http/service/index';
import URL from '@/http/url.js'
const jobs = () => import(/* webpackChunkName:"jobs" */'./component/jobs')
const lazyChangeGeographicLocation = () => import('@/components/addrChooseList/components/change-geographic-location.vue')

const isDef = function (v) {
  return v !== undefined && v !== null
}
const testNumber = (target) => {
  if (isDef(target)) {
    const reg = new RegExp('^[0-9]*(\\.\\d*)?$');
    return reg.test(target)
  }
  return false;
}
const testSaleNumber = (target) => {
  if (isDef(target)) {
    const reg = new RegExp('^[0-9]{1,9}(\\.[0-9]{0,2})?$');
    return reg.test(target) && Number(target) !== 0
  }
  return false;
}
const testValidate = (target, min, max) => {
  if (isDef(target)) {
    return (typeof target === 'string' && target.length > 0 && testValidateString(target, min, max)) || (typeof target !== 'string');
  }
  return false;
}
const testValidateString = (t, min = 1, max = 100) => {
  const reg = /^[a-zA-Z0-9_\-\u4e00-\u9fa5]+$/
  return reg.test(t) && t.length >= min && t.length <= max;
}
const testZHValidateString = (t, min = 1, max = 100) => {
  const reg = /^[\u4e00-\u9fa5·•]+$/
  return reg.test(t) && t.length >= min && t.length <= max;
}
const testValidateStringWithSyntax = (t, min = 2, max = 40) => {
  const reg = new RegExp(`^[a-zA-Z0-9_\\-\\u4e00-\\u9fa5//.&()]{${min},${max}}$`)
  return reg.test(t)
}

export default {
  name: 'apply-partner',
  components: {
    skeleton,
    // eslint-disable-next-line
    item,
    'stage-0': Stage0,
    'stage-1': Stage1,
    'stage-2': Stage2,
    lazyChangeGeographicLocation,
    jobs,
    'VanStep': Step,
    'VanSteps': Steps,
  },
  data() {
    return {
      isSyn: utils.isSyBird() || utils.isSyBirdMP(),
      documentId: '1481',
      isSkeletonLoading: true,
      stage: 0, // 0未实名 1已实名 2填写信息 3进度查询
      checked: false,
      name: '',
      monthExpectSaleAmount: '', // 预计月销售额
      identify: '', // 身份证信息
      mobile: '',
      canEditMobile: true, // 手机号是否可编辑
      showDateChange: false,
      showChooseLoaction: false,
      showJobs: false,
      showFailDialog: false,
      info: {
        people: [],
        community: [],
        historyRelyStoreList: []
      },
      submitInfo: {},
      userIdentify: 0, // 用户身份
      date: new Date(),
      maxDate: new Date(),
      minDate: new Date(1970, 0, 1),
      progressInfo: [
        {
          title: '实名认证'
        },
        {
          title: '填写信息'
        },
        {
          title: '审核'
        },
      ],
      verifyInfo: [],
      communityTarget: null,
      peopleTarget: null,
      showCommunity: false,
      showPeople: false,
      unionId: undefined,
      isBindWx: false,
      realNameAuthInfo: null,
    }
  },
  computed: {
    interfaceTitle() {
      return (utils.isSyBird() || utils.isSyBirdMP()) ? '三翼鸟' : '海尔智家'
    },
    invisiableBack() {
      return utils.isSyBirdMP()
    },
    needBindWx() {
      return this.$route.query.activityId && this.$route.query.inviteStoreId && !this.isBindWx
    },
    displayMobile() {
      return this.mobile.replace(/^(\d{3})\d+(\d{4})$/, '$1****$2');
    },
    readOnly() {
      return this.stage !== 0;
    },
    progressIndex() {
      const progIndex = this.stage <= 1 ? 0 : (this.stage - 1);
      console.log('当前进度：', progIndex);
      return this.stage <= 1 ? 0 : (this.stage === 3 && this.verifyIndex === 2) ? 4 : (this.stage - 1);
    },
    verifyIndex() {
      if (this.verifyInfo) {
        let res = 0;
        this.verifyInfo.some((item, index) => {
          if (!isDef(item.time)) {
            res = index - 1;
            return true;
          } else { // 最后一步已完成
            res = index;
          }
        });
        return res;
      }
      return 0;
    }
  },
  methods: {
    _fetchInit() {
      myManageinfo(null, true).then(({ data }) => {
        this.isSkeletonLoading = false;
        this.isBindWx = data.data.bindWx
        if (this.needBindWx) {
          this._avoidCheat();
        }
        if (data.success && data.data) {
          // identifyFlag 为0 normalMicroUser： 1-普通用户 2-储备微店主
          this.userIdentify = (data.data.identifyFlag === 0 && data.data.normalMicroUser === 2) ? -1 : data.data.identifyFlag;
          if (isDef(data.data.mobile)) {
            this.mobile = data.data.mobile
            this.canEditMobile = false;
          }
          if (this.userIdentify > 0) {
            let message = '您已是'
            switch (this.userIdentify) {
              case 21:
              case 22:
                message += '店铺合伙人'
                break;
              case 31:
                message += '平台合伙人'
                break;
              default:
                message += '商家身份'
                break;
            }
            this.$toast(message);
            setTimeout(() => {
              this.$store.dispatch('doHistoryBack');
            }, 1000);
          } else {
            this._fetchSubmitData();
          }
        } else {
          this.$toast(data.message);
        }
      });
    },
    /**
     * 获取提交信息
     */
    _fetchSubmitData() {
      applyResult({ time: Date.now() }).then(res => {
        // #region 111
        // const data = res.data.data;
        // if (data.applied) {
        //   this.stage = 3;
        //   this.verifyInfo = data.nodeList;
        //   this.isSkeletonLoading = false;
        // } else {
        //   this._fetchBindData();
        // }
        // #endregion
        this._fetchBindData();
      })
    },
    /**
     * 获取填写信息页面信息
     */
    _fetchQueryApplyInfo() {
      return queryApplyInfo().then(res => {
        const { data, code, msg } = res.data
        if (code === 200) {
          this.info = Object.assign(this.info, data);
          this.stage = 2;
        } else {
          this.$toast(msg);
        }
      }).catch(e => {
        this.$toast(e.message);
      })
    },
    /**
     * 获取认证信息
     */
    _fetchBindData() {
      this.$http.get(URL.TRUE_REALNAMEAUTH).then(res => {
        const result = res.data.data;
        console.log(result);
        this.stage = 0;
        if (result && result.isAuth && result.identity && result.identity.identityNo && result.identity.realName) {
          this.realNameAuthInfo = result.identity
          // 非三翼鸟环境 带出身份证和姓名
          if (!(utils.isSyBird() || utils.isSyBirdMP())) {
            this.stage = 1;
            this.name = result.identity.realName.replace(/^([\u4e00-\u9fa5]{1})[\u4e00-\u9fa5]+([\u4e00-\u9fa5]{1})$/, '$1*$2');
            this.identify = result.identity.identityNo.replace(/^(\d{3})\d+([\dX]{4})$/, '$1********$2');
          }
          this.mobile = result.identity.mobile;
          this.canEditMobile = false;
        }
        this.isSkeletonLoading = false;
      }).catch(e => {
        this.$toast(e.message);
      })
    },
    /**
     * 提交实名认证
     */
    _submitBindData() {
      // 校验
      if (!testZHValidateString(this.name, 2, 50)) { this.$toast('请按规则输入您的中文姓名（2-50 个汉字，支持·）'); return Promise.resolve(false) }
      if (!testValidate(this.identify)) { this.$toast('请输入正确的身份证号'); return Promise.resolve(false) }
      if (!testValidate(this.mobile)) { this.$toast('请输入正确的手机号'); return Promise.resolve(false) }

      return new Promise((resolve, reject) => {
        const url = `${URL.POST_USERDATAANDID}?realName=${this.name}&identityNo=${this.identify}&mobile=${this.mobile}&channal=PARTNER`
        this.$http.post(url).then(res => {
          const result = res.data;
          if (result.data) {
            this.$toast('认证成功')
            resolve(true);
          } else {
            this.$toast(result.message)
            resolve(false);
          }
        }).catch(e => {
          reject(e);
        })
      })
    },
    /**
     * 提交申请信息
     */
    _submitApplyInfo() {
      // 校验
      if (!isDef(this.info.nickname) || this.info.nickname.length === 0) { this.$toast('请填写昵称'); return }
      if (!testValidate(this.info.nickname, 2, 20)) { this.$toast('请填写正确的昵称，2-20个中文、英文、数字、_、-的组合'); return }
      if (testNumber(this.info.nickname)) { this.$toast('昵称不能为纯数字'); return }
      if (!testValidate(this.info.job)) { this.$toast('请选择从事职业'); return }
      if (!testValidate(this.info.date)) { this.$toast('请选择工作时间'); return }
      if (!testValidate(this.info.location)) { this.$toast('请选择所在地'); return }
      // if (!isDef(this.info.community) || this.info.community.length === 0) { this.$toast('请填写可覆盖社区'); return }
      // if (!testValidate(this.info.community, 2, 40)) { this.$toast('请输入正确的社区名称'); return }
      // if (!isDef(this.info.people) || this.info.people.length === 0) { this.$toast('请填写所接触人群'); return }
      if (this.userIdentify === 0) {
        if (this.monthExpectSaleAmount.length === 0) {
          this.$toast('请填写预计月销额'); return
        } else if (!testNumber(this.monthExpectSaleAmount)) {
          this.$toast('请填写正确的预计月销额'); return
        } else if (!testSaleNumber(this.monthExpectSaleAmount)) {
          this.$toast('请填写0.01-999999999.99预计月销额，最多支持两位小数'); return
        }
      }
      const historyRelyStoreList = this.info.historyRelyStoreList.map(item => { return { 'storeId': item.storeId } })
      this.submitInfo = {
        nickname: this.info.nickname,
        occupation: this.info.job.code,
        workBeginTime: `${this.date.getFullYear()}-${this.date.getMonth() + 1}-1`,
        residentAddress: {
          province: this.info.location.provinceId,
          city: this.info.location.cityId,
          region: this.info.location.districtId,
          street: this.info.location.townshipId,
          fullAddress: this.info.location.region
        },
        community: this.info.community ? this.info.community.join(',') : '',
        contactCrowd: this.info.people ? this.info.people.join(',') : '',
        validSaleStartDate: this.info.validSaleStartDate,
        validSaleEndDate: this.info.validSaleEndDate,
        monthExpectSaleAmount: this.monthExpectSaleAmount,
        historySaleCount: this.info.historySaleCount,
        historySaleAmount: this.info.historySaleAmount,
        agreement: true,
        historyRelyStoreList,
        activityId: this.$route.query.activityId ? this.$route.query.activityId : undefined,
        inviteStoreId: this.$route.query.inviteStoreId	? this.$route.query.inviteStoreId	: undefined,
      }
      submitApplyInfo(this.submitInfo).then(res => {
        const { msg, code } = res.data
        if (code !== 200) {
          this.$toast(msg);
        } else {
          // this._fetchSubmitData();
          // 提交成功后 直接返回
          this.$toast('平台合伙人申请成功');
          setTimeout(() => {
            this.$store.dispatch('doHistoryBack');
          }, 1000);
        }
      }).catch(e => {
        this.$toast('提交失败，请稍后重试:' + e.message);
      })
    },
    /**
     * 实名认证 确认
     */
    _diaConfirm() {
      // 先实名认证
      const that = this;
      Dialog.confirm({
        message: '确定对当前账号进行实名认证吗',
        beforeClose(action, done) {
          if (action === 'confirm') {
            const realNameAuthInfo = that.realNameAuthInfo
            if ((utils.isSyBirdMP() || utils.isSyBird()) && realNameAuthInfo && realNameAuthInfo.realName && realNameAuthInfo.identityNo && realNameAuthInfo.mobile) {
              done()
              if (realNameAuthInfo.realName === that.name && realNameAuthInfo.identityNo?.toLocaleLowerCase() === that.identify?.toLocaleLowerCase() && realNameAuthInfo.mobile === that.mobile) {
                that.stage = 2;
              } else {
                that.$toast('您当前账号已认证其他身份证信息')
              }
            } else {
              that._submitBindData().then((res) => {
                if (res) {
                  done()
                  that._fetchQueryApplyInfo().then(() => {
                    that.stage = 2;
                  });
                } else {
                  done()
                  // that.showFailDialog = true;
                }
              })
            }
          } else {
            done();
          }
        },
      });
    },
    /**
     * 下一步
    */
    fnNextStep() {
      // 检测实名认证步骤协议
      if (this.stage <= 1 && !this.checked) {
        this.$toast('请阅读平台合伙人协议，并勾选')
        return;
      }
      if (this.stage === 0) {
        // 绑定前拦截绑定
        if (this.needBindWx) {
          if (this.unionId) { // 未绑定微信 并且已授权
            this._bindWxAccount()
          } else { // 未绑定微信 没有授权
            this._doGetWxCode()
          }
        } else {
          this._diaConfirm()
        }
      } else if (this.stage === 1) {
        if (this.needBindWx) {
          if (this.unionId) { // 未绑定微信 并且已授权
            this._bindWxAccount()
          } else { // 未绑定微信 没有授权
            this._doGetWxCode()
          }
        } else {
          // 去第二步骤
          this._fetchQueryApplyInfo().then(() => {
            this.stage = 2;
          });
        }
      } else if (this.stage === 2) {
        // 提交资料
        this._submitApplyInfo();
      }
    },
    fnNicknameChange(e) {
      const value = e.target.value ? e.target.value.trim() : '';
      this.info.nickname = value;
    },
    fnCommunityChange(e) {
      this.communityTarget = e.target;
      if (!isDef(this.info.community)) {
        this.info.community = [];
      }
      if (isDef(this.communityTarget)) {
        const value = this.communityTarget.value ? this.communityTarget.value.trim() : '';
        if (value.length === 0) {
          return;
        }
        if (!testValidateStringWithSyntax(value, 2, 20)) {
          this.$toast('请输入正确的社区名称，支持4-40个字符!');
          return;
        }
        if (this.info.community.indexOf(value) < 0) {
          this.info.community.push(value)
        }
      }
      this.communityTarget.value = '';
      this.showCommunity = false;
    },
    fnCommunityAdd() {
      if (!isDef(this.info.community)) {
        this.info.community = []
      }
      if (this.info.community.length >= 15) {
        this.$toast('只支持15个自定义社区！');
        return;
      }
      this.showCommunity = true;
    },
    fnCommunityClose(text) {
      isDef(this.info.community) &&
       this.info.community.indexOf(text) >= 0 &&
        this.info.community.splice(this.info.community.indexOf(text), 1)
    },
    fnPeopleChange(e) {
      this.peopleTarget = e.target;
      if (!isDef(this.info.people)) {
        this.info.people = [];
      }
      if (isDef(this.peopleTarget)) {
        const value = this.peopleTarget.value ? this.peopleTarget.value.trim() : '';
        if (value.length === 0) {
          return;
        }
        if (!testValidateStringWithSyntax(value, 1, 20)) {
          this.$toast('请输入正确的人群名称，支持2-40个字符！');
          return;
        }
        if (this.info.people.indexOf(value) < 0) {
          this.info.people.push(value)
        }
      }
      this.peopleTarget.value = '';
      this.showPeople = false;
    },
    fnPeopleAdd() {
      if (!isDef(this.info.people)) {
        this.info.people = []
      }
      if (this.info.people.length >= 15) {
        this.$toast('只支持15个自定义人群!');
        return;
      }
      this.showPeople = true;
    },
    fnPeopleClose(text) {
      isDef(this.info.people) &&
       this.info.people.indexOf(text) >= 0 &&
        this.info.people.splice(this.info.people.indexOf(text), 1)
    },
    fnDateChange() {
      this.showDateChange = true;
    },
    fnConfirmDate() {
      const m = parseInt(this.date.getMonth()) + 1;
      this.info.date = `${this.date.getFullYear()}年${m}月`
      this.showDateChange = false;
    },
    fnJobChange() {
      this.showJobs = true;
    },
    fnMonthExpectSaleAmountChange(e) {
      const value = e.target.value ? e.target.value.trim() : '';
      this.monthExpectSaleAmount = value;
    },
    fnConfirmJobChange(value) {
      console.log(value);
      this.info.job = value
      this.showJobs = false;
    },
    fnlocationChange() {
      this.showChooseLoaction = true;
    },
    fnOnHide() {
      this.showChooseLoaction = false;
      this.showJobs = false;
      this.showJobs = false;
    },
    fnConfirmLocation(value) {
      console.log(value);
      this.showChooseLoaction = false;
      this.info.location = value;
    },
    // 实名阶段
    fnIdChange(e) {
      const value = e.target.value ? e.target.value.trim() : '';
      this.identify = value;
    },
    fnNameChange(e) {
      const value = e.target.value ? e.target.value.trim() : '';
      this.name = value;
    },
    fnMobileChange(e) {
      this.mobile = e.target.value ? e.target.value.trim() : '';
    },
    fnLicenseChecked(e) {
      this.checked = !this.checked;
    },
    fnReApply() {
      // 重新提交
      this._fetchQueryApplyInfo().then(() => {
        this.stage = 2;
      });
    },
    fnBack() {
      if (this.stage === 2) {
        this._fetchBindData();
      } else {
        this.$store.dispatch('doHistoryBack')
      }
    },
    _formatter(type, val) {
      if (type === 'year') {
        return `${val}年`;
      } else if (type === 'month') {
        return `${val}月`;
      }
      return val;
    },
    // 微信授权
    _doGetWxCode() {
      let redirectUrl = location.href
      utils.getWxUserInfoCode('wxb03a523baf487993', redirectUrl);
    },
    _avoidCheat() {
      // if have code in query ,do bind ,if not go getwxcode
      const code = this.$route.query.code
      if (code) {
        // 反作弊逻辑 页面打开时 如果有邀请和活动ID 则必须在微信环境下打开
        const _query = this.$route.query;
        const that = this;
        if (_query.activityId && _query.inviteStoreId) {
          that.$http.get(URL.getWechatUserInfo, { params: { code: _query.code } }).then(({ data }) => {
            if (data.success) {
              that.unionId = data.data.unionid
              if (data.data.unionid) {
                that._bindWxAccount()
              } else {
                this.$toast({ message: '获取UnionId失败，请刷新页面', duration: 0 })
              }
            } else {
              // 授权失败
              this.$toast({ message: data.message, duration: 0 })
            }
          })
        }
      } else {
        this._doGetWxCode()
      }
    },
    // 退出登录
    _logout() {
      this.$store.dispatch('logout', {
        path: '/partners',
        query: {
          activityId: this.$route.query.activityId || '',
          share: this.$route.query.inviteStoreId || '',
          sg_rn_app: this.$route.query.sg_rn_app || '',
        }
      })
      // const data = {};
      // personalApiIndex.logout(data).then(res => {
      //   const _resData = res.data;
      //   if (_resData.success) {
      //     // 清除localStorage缓存
      //     localStorage.clear();
      //     // 清除store的数据
      //     this.$store.dispatch('resetStore')
      //     gio && gio('clearUserId');
      //     // 跳转到登陆页面
      //     this.$router.replace({
      //       path: '/partners',
      //       query: {
      //         activityId: this.$route.query.activityId || '',
      //         share: this.$route.query.inviteStoreId || '',
      //         sg_rn_app: this.$route.query.sg_rn_app || '',
      //       }
      //     });
      //   } else {
      //     this.$toast.fail(_resData.message);
      //   }
      // })
    },
    _bindWxAccount() {
      bindWxAccount({ unionId: this.unionId, bindType: 7 }).then((result) => {
        const { message, success } = result.data
        if (success) {
          this._fetchInit();
        } else {
          this.$toast({ message: message,
            duration: 3000,
            onClose: () => {
              this._logout();
            } })
        }
      }).catch((err) => {
        this.$toast({ message: err.message, duration: 3000 })
      });
    }
  },
  mounted() {
    if (utils.isSyBird()) {
      this.documentId = '1485'
    }
    if (this.$route.query.activityId && this.$route.query.inviteStoreId && !utils.isWeixin()) {
      this.$toast({ message: '请在微信环境打开邀请链接', duration: 0 })
    } else {
      setTimeout(() => {
        this._fetchInit();
      }, 600);
    }
  }
}
</script>
